import React from 'react';
import { TabContent, TabPane, Row, Col } from 'reactstrap';
import classnames from 'classnames';
import 'bootstrap/dist/css/bootstrap.min.css';

function Item(props){
return(

<div className="single-menu-list row justify-content-between align-items-center">
                            
        <div className="col-lg-9">
       
         <a id="a" href="#a"><h4>{props.itemname}</h4></a>
         <p>
 
           {props.itemdescription}
        
         </p>
                            
         </div>

       <div className="col-lg-3 flex-row d-flex price-size">
                          
          <div className="s-price col">
                                
                <h4>Price</h4>
                <span>{props.itemprice}</span>
                                
          </div>
          
          <div className="s-price col">
                                
                <h4>
                  
											<input type="checkbox" id="primary-checkbox"/>
											
                </h4>
                                                
          </div>																				
         </div>
  </div>
  
  
  )

}
var style={
    backgroundColor:'Transparent',
    backgroundRepeat:'no-repeat',
    border:'none',
    cursor:'pointer',
    overflow:'hidden'     
}

class Menu extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      activeTab: '1',
  
    };

   this.state={

    data:[]
   }

}




  toggle(tab) {

    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }
 
  componentWillMount() {
  fetch("http://localhost:1234/giveitems")
    .then(res=>res.json()).then((res)=>{
     this.setState({data:res});
    })
  }
 
  submit(){
    
    document.getElementById('menu').submit(function(){
      alert("Submitted");
    });

  }

  render(){
 
    return (
      <section className="menu-list-area section-gap">
      <form action="/checkout" method="post" id="menu" encType="multipart/form-data" >
      <div className="container">
      <div className="row">                   
             
        <div className="menu-cat mx-auto" >
  
              <ul className="nav nav-pills" id="pills-tab" role="tablist">
            
                          <li className="nav-item">
            
                            <a    className={classnames({ active: this.state.activeTab === '1' })}
                             onClick={() => { this.toggle('1');  }} href="#1">Hair</a>
            
                          </li>
            
                          <li className="nav-item">
            
                            <a    className={classnames({ active: this.state.activeTab === '2' })}
              onClick={() => { this.toggle('2'); }} href="#2">Skin</a>
            
                          </li>
                          <li className="nav-item">
            
            <a className={classnames({ active: this.state.activeTab === '3' })}
onClick={() => { this.toggle('3');}} href="#3" >Face</a>

          </li>

             
          <li className="nav-item">
            
            <a    className={classnames({ active: this.state.activeTab === '4' })}
onClick={() => { this.toggle('4'); }} href="#4" >Nails</a>

          </li>

             </ul>       
                </div>
          </div>
    
        <TabContent activeTab={this.state.activeTab} name="menucontent">
          <TabPane tabId="1">
            <Row>
              <Col sm="12">
              {this.state.data.filter(item=>item.itemcatogery==='hair').map(item=><Item key={item.itemname}  itemname={item.itemname} itemdescription={item.itemdescription}
              itemprice={item.price}/>)}
              </Col>
            </Row>
          </TabPane>

          <TabPane tabId="2">
        
            <Row>
              <Col sm="12">
              {this.state.data.filter(item=>item.itemcatogery==='skin').map(item=><Item key={item.itemname}  itemname={item.itemname} itemdescription={item.itemdescription}
              itemprice={item.price}/>)}
              
              </Col>
            </Row>

          </TabPane>
          <TabPane tabId="3">
           
          <Row>
              <Col sm="12">
              {this.state.data.filter(item=>item.itemcatogery==='face').map(item=><Item key={item.itemname}  itemname={item.itemname} itemdescription={item.itemdescription}
              itemprice={item.price}/>)}
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="4">
            <Row>
            <Col sm="12">
            {this.state.data.filter(item=>item.itemcatogery==='nails').map(item=><Item key={item.itemname} itemname={item.itemname} itemdescription={item.itemdescription}
              itemprice={item.price}/>)}     
         
              </Col>
            </Row>
          </TabPane>
          <div class="d-flex flex-row-reverse">
            
            <div class="p-2"><button type="submit"style={style}><a class="genric-btn primary circle arrow">Proceed<span class="lnr lnr-arrow-right"></span></a></button></div>
      </div>
        </TabContent>
   </div>      
        
      </form>

</section>
    );
  }
}
export default Menu;
